import Vue from "vue";

export default {
  install(vue) {
    vue.prototype.$broadcast = null; // —Å—Å—ã–ª–∫–∞ –¥–ª—è –¥–æ—Å—Ç—É–ø–∞ –∫ —à–∏–Ω–µ
    vue.mixin({
      beforeCreate() {  // —Ö—É–∫ —Å–æ–±—ã—Ç–∏—è
        if (this.$options.$module) {  // –µ—Å–ª–∏ –ø–æ–ª–µ $module –∑–∞–¥–∞–Ω–æ –≤ —Å–∞–º–æ–º –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–µ,
          this.$broadcast = new Vue();  // —Å–æ–∑–¥–∞–µ–º –Ω–æ–≤—É—é —à–∏–Ω—É
        } else if (this.$parent && this.$parent.$broadcast) {  // –µ—Å–ª–∏ –ø–æ–ª–µ $module –Ω–µ –∑–∞–¥–∞–Ω–æ –≤ —Å–∞–º–æ–º –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–µ,
          this.$broadcast = this.$parent.$broadcast;  // –ø–µ—Ä–µ–¥–∞–¥–∏–º —Å—Å—ã–ª–∫—É –Ω–∞ —Ä–æ–¥–∏—Ç–µ–ª—å—Å–∫—É—é —á–µ—Ä–µ–∑ –ø–æ–ª–µ $parent
        }
      },
    });
  },
}